import AlbumGrid from '../../components/albums/AlbumGrid';
import PhotoTabs from '../../components/albums/PhotoTabs';
import Button from '@/components/ui/Buttom'; // Corregí "Buttom" a "Button"
import Card from '../../components/ui/Card';

export default function PhotosPage() {
  return (
    <div className="container">
      <div className="row g-4">
        {/* Sidebar - Omitido por brevedad (similar al anterior) */}
        
        {/* Main content */}
        <div className="col-md-8 col-lg-6 vstack gap-4">
          <Card>
            <div className="card-header d-sm-flex text-center align-items-center justify-content-between border-0 pb-0">
              <h1 className="card-title h4">Photos</h1>
              <Button variant="primary" icon="plus" dataBsToggle="modal" dataBsTarget="#modalCreateAlbum">
                Create album
              </Button>
            </div>
            
            <div className="card-body">
              <PhotoTabs />
              <AlbumGrid />
            </div>
          </Card>
        </div>
      </div>
      
      {/* Modal - Se implementará como componente aparte */}
    </div>
  );
}